<template>
    <div class="formlayout"  :style="setForm" ref="formlayout">
       <div
          class="formlayout-Item"
          v-for="(options,index) in formList"
          :key="index"
          v-if="options.show"
          :style="setDiv(options)"
        >
              <slot :item="options.item" v-if="!options.item.soltName" name="content">
                <!--  -->
                  <slot name="label" :item="options.item">
                      <div ref="label" class="formlayout-label"><i v-if="options.item.required">*</i>{{options.item.label}}</div>
                  </slot>
                <!--  -->
                <div class="formlayout-div">
                  <template v-if="options.item.vHtml">
                    <!-- v-text -->
                    <component :is="options.item.type"
                    v-on="options.item.event"
                    v-html="options.item.vHtml"
                    :key="index"
                    v-bind="options.item.props">
                        <slot v-if="options.item.type ==='Select'">
                            <Option v-for="item in options.item.props.options"
                            :value="item.value" :key="item.value">{{ item.label }}
                              <span v-if="item.html"  v-html="item.html"></span>
                            </Option>
                        </slot>
                    </component>

                  </template>
                  <template v-if="!options.item.vHtml">  
                    <component :is="options.item.type"
                    v-on="options.item.event"
                    v-model="options.item.props.value"
                    :key="index"
                    v-bind="options.item.props">
                        <slot v-if="options.item.type ==='Select'">
                            <Option v-for="item in options.item.props.options"
                            :value="item.value" :key="item.value">{{ item.label }}
                              <span v-if="item.html"  v-html="item.html"></span>
                            </Option>
                        </slot>
                    </component>               
                  
                  </template>


                 
                  
                </div>
              </slot>
              <slot name="tip" :item="options.item">
              </slot> 
                <!--  -->
               <slot :name="options.item.soltName" :item="options.item" v-if="options.item.soltName"></slot>

      </div>
    </div>
</template>
<script>
  import layoutAlgorithm from '../../utils/layoutAlgorithm';

  import burgeonConfig from '../../assets/config';

export default {
    name:'FormLayout',
    props:{
      defaultColumn: {
        //   
        type: Number,
        default: 4
      },
      labelWidth:{
         //   
        type: Number,
        default: 120

      },
      defaultconfig: {
        //   
        type: Array,
        default() {
          return [];
        }
      }
    },
    data(){
      return {
        formList: [],
        newdefaultconfig:[],
        type:[],
        verify:[]
      };
    },
    computed: {
      //  div 
      setDiv() {
        let {userAgent} = navigator;
        let rMsie = /(msie\s|trident.*rv:)([\w.]+)/;
       let match = rMsie.exec(userAgent.toLowerCase());

        if (match === null){
           return item => ` grid-column:${item.x}/${item.col + item.x};grid-row:${
          item.y
        }/${item.y + item.row};`;

        } else {
           return item => ` 
        
         grid-column:${item.x};
         
        -ms-grid-columns:${item.x};
        grid-column-span:${item.col};
        -ms-grid-column-span:${item.col};
        -ms-grid-row:${item.y};
        -ms-grid-row-span:${item.row};
        grid-row-span:${item.row};
          grid-row:${item.y};`;

        }
  
      },
      setForm(){
        let width = (100/this.defaultColumn).toFixed(2);
        let fr = new Array(this.defaultColumn).fill('1fr').join(' ');
        return `display: -ms-grid; grid-columns: ${fr};grid-template-columns: repeat(${this.defaultColumn}, ${width}%);`;
      }

    },
    watch: {
      defaultconfig: {
          handler (val,old) {
            this.formList = this.dataColRol();
          },
          deep: true
      },
      defaultColumn:{
         handler (val,old) {
            this.formList = this.dataColRol();
          },
          deep: true
      }
    },
    methods:{
       dataColRol() {
        // 
        const list = layoutAlgorithm(this.defaultColumn ,this.defaultconfig);
        return Object.keys(list).reduce((temp, current) => {
           if (list[current].show !== false){
              list[current].show = true;
           }
          temp.push(list[current]);
          return temp;
        }, []);
      },
      verifyMessage(){
        //  
        this.verify = [];
        this.formList.forEach((item,index) => {
          //   
           if (item.item.props.value && typeof item.item.props.value === 'string') {
            item.item.props.value = item.item.props.value.replace(/^\s+|\s+$/g, '');
          }
            if (item.item && item.item.required && item.show!==false){
                if (item.item){
                    if ((!item.item.props.value && item.item.props.value!== 0) || JSON.stringify(item.item.props.value) ==='[]'){
                          this.verify.push({
                            index:index,
                            tip:`${item.item.label}`
                          });
                          //return `${item.item.label}`;
                    } else if (item.item.props.regx){
                       let {value} = item.item.props;
                       item.item.props.regx.lastIndex = 0;
                       let checked = item.item.props.regx.test(value);
                        if (!checked){
                           this.verify.push({
                            index:index,
                            tip:`${item.item.label}`
                          });
                        }
                    }
                }
            }
        });
        return this.verify;
      }

    },
    mounted(){
      this.formList = this.dataColRol();
    }
};
</script>





